import { EventEmitter, Injectable, TemplateRef } from '@angular/core';

class ButtonTabModel {
  public id: number;
  public title: string | TemplateRef<any> | any;
  public active: boolean;
  constructor(
    id: number = 0, title: string | TemplateRef<any> | any = '', active: boolean = false
  ) {
    this.id = id;
    this.title = title;
    this.active = active;
  }
}

@Injectable()
export class TabsService {
  /**
   * button
   */
  buttons: ButtonTabModel[];
  /**
   * button activated
   */
  buttonActivated: EventEmitter<ButtonTabModel>;
  /**
   * button activated val
   */
  buttonActivatedVal: ButtonTabModel;
  /**
   * Prevent tab
   */
  preventTabs: number;
  /**
   * Tabs reloaded
   */
  tabsReloaded: EventEmitter<boolean>;
  /**
   * Check active
   */
  checkActive: EventEmitter<void>;
  /**
   * Loading complete
   */
  loadComplete: boolean;
  /**
   * Height tab active
   */
  heightTabActive: number;
  /**
   * boolean
   */
  animationsActivated!: boolean;

  constructor() {
    this.buttons = [];
    this.buttonActivated = new EventEmitter();
    this.buttonActivatedVal = new ButtonTabModel();
    this.preventTabs = 0;
    this.tabsReloaded = new EventEmitter();
    this.checkActive = new EventEmitter();
    this.loadComplete = false;
    this.heightTabActive = 0;
    this.heightTabActive = 0;
  }
}
