import { AfterViewInit, Component, ElementRef, HostBinding, Input, OnDestroy, OnInit, Renderer2, TemplateRef, ViewChild } from "@angular/core";
import { animate, keyframes, style, transition, trigger } from "@angular/animations";
/* SERVICE */
import { GdaTabsService } from "./gda-tabs.service";
import { TabsService } from "./tabs.service";
/* RXJS */
import { delay, of, Subscription } from "rxjs";

class ButtonTabModel {
  public id: number;
  public title: string | TemplateRef<any> | any;
  public active: boolean;
  constructor(
    id: number = 0, title: string | TemplateRef<any> | any = '', active: boolean = false
  ) {
    this.id = id;
    this.title = title;
    this.active = active;
  }
}

@Component({
  selector: 'gda-tab',
  template: `
        <div 
            class="gda-tab-content"
            #content
            *ngIf="enabled" 
            [@tabsAnimation]="{value: '*', params: { leftEnter: leftEnter, leftLeave: leftLeave, animations: animations ? '0.5s ease-in-out' : '0s'}}">
            <ng-content></ng-content>
        </div>    
    `,
  animations: [
    trigger('tabsAnimation', [
      transition(':enter', [
        style({ position: 'absolute', left: '{{ leftEnter }}' }),
        animate('{{ animations }}',
          keyframes([
            style({
              left: '{{ leftEnter }}'
            }),
            style({
              left: 0
            })
          ])
        )
      ]),
      transition(':leave', [
        style({ position: 'absolute', left: 0 }),
        animate('{{ animations }}',
          keyframes([
            style({
              left: 0
            }),
            style({
              left: '{{ leftLeave }}'
            })
          ])
        )
      ])
    ])
  ]
})
export class GdaTabComponent implements OnInit, AfterViewInit, OnDestroy {
  /**
   * Class
   */
  @HostBinding('class.gda-tab') setClass = true;
  /**
   * Content
   */
  @ViewChild('content', { static: false }) contentEl!: ElementRef;
  /**
   * Titolo del tab
   */
  @Input() titleTab: string | TemplateRef<any> | any;
  /**
   * Apertura all avvio
   */
  @Input() isOpen = false;
  /**
   * Animations
   */
  public animations!: boolean;
  /**
   * Bottone corrispondente al contenuto
   */
  private button!: ButtonTabModel;
  /**
   * id del Tab
   */
  private id!: number;
  /**
   * Enabled
   */
  public enabled: boolean;
  /**
   * Entrata
   */
  public leftEnter: number | string;
  /**
   * Uscita
   */
  public leftLeave: number | string;
  /**
   * Subscription
   */
  private sub1: Subscription;
  private sub2: Subscription;
  private sub3: Subscription;

  constructor(
    private elementRef: ElementRef,
    private renderer: Renderer2,
    private gdaTabsService: GdaTabsService,
    private tabsService: TabsService
  ) {
    this.titleTab = '';
    this.leftEnter = 0;
    this.leftLeave = 0;
    this.button = new ButtonTabModel();
    this.enabled = false;
    this.sub1 = this.tabsService.buttonActivated.subscribe((button: ButtonTabModel) => {
      this.button.id = +this.elementRef.nativeElement.getAttribute('data-gda-tab');
      this.id = +this.elementRef.nativeElement.getAttribute('data-gda-tab');
      this.button.active = (this.button.id === button.id);
      if (this.button.active) {
        this.tabsService.buttonActivatedVal = button;
      }
      if (this.tabsService.animationsActivated !== undefined) {
        this.animations = this.tabsService.animationsActivated;
      } else if (this.gdaTabsService.animationsActivated !== undefined) {
        this.animations = this.gdaTabsService.animationsActivated;
      } else {
        this.animations = true;
      }
      if (this.id === button.id) {
        this.button = button;
        if (this.tabsService.preventTabs > this.button?.id) {
          this.leftEnter = '-100%';
        } else if (this.tabsService.preventTabs < this.button?.id) {
          this.leftEnter = '100%';
        }
      } else if (this.tabsService.preventTabs === this.button?.id) {
        if (button.id > this.button?.id) {
          this.leftLeave = '-100%';
        } else if (button.id < this.button?.id) {
          this.leftLeave = '100%';
        }
      } else {
        this.animations = false;
      }
      of(true).pipe(delay(0)).subscribe(() => {
        this.enabled = (this.button?.active);
        of(true).pipe(delay(0)).subscribe(() => {
          if (this.contentEl?.nativeElement) {
            this.tabsService.heightTabActive = this.contentEl.nativeElement.offsetHeight;
          }
          this.animations = false;
        });
      });
    });
    this.sub2 = this.tabsService.tabsReloaded.subscribe(() => {
      this.id = +this.elementRef.nativeElement.getAttribute('data-gda-tab');
      if (!this.tabsService.buttons.some((button: ButtonTabModel) => button.id === this.id)) {
        this.tabsService.buttons.push(
          new ButtonTabModel(this.id, this.titleTab, false)
        );
      }
      this.tabsService.buttons.sort((a: ButtonTabModel, b: ButtonTabModel) => {
        if (a.id < b.id) {
          return -1;
        }
        if (a.id > b.id) {
          return 1;
        }
        return 0;
      });
    })
    this.sub3 = this.tabsService.checkActive.subscribe(() => {
      this.id = +this.elementRef.nativeElement.getAttribute('data-gda-tab');
      const button = this.tabsService.buttons.find((button) => button.id === this.id);
      this.enabled = button?.active || false;
      if (button?.active) {
        this.tabsService.buttonActivatedVal = button;
      }
      if (button) {
        this.tabsService.buttonActivated.emit(button);
      }
    });
  }

  ngOnInit(): void {
    if (this.tabsService.animationsActivated !== undefined) {
      this.animations = this.tabsService.animationsActivated;
    } else if (this.gdaTabsService.animationsActivated !== undefined) {
      this.animations = this.gdaTabsService.animationsActivated;
    } else {
      this.animations = true;
    }
  }

  ngAfterViewInit(): void {
    const parent = this.renderer.parentNode(this.elementRef.nativeElement);
    parent.querySelectorAll('gda-tab').forEach((e: HTMLDivElement, i: number) => {
      e.setAttribute('data-gda-tab', String(i));
    });
    this.id = +this.elementRef.nativeElement.getAttribute('data-gda-tab');
    const button: ButtonTabModel = new ButtonTabModel(this.id, this.titleTab, this.tabsService.buttons.some((button: ButtonTabModel) => button.active) ? false : this.isOpen);
    this.tabsService.buttons.push(
      button
    );
    if (button.active) {
      of(true).pipe(delay(0)).subscribe(() => {
        this.enabled = true;
        this.tabsService.buttonActivatedVal = button;
        this.tabsService.preventTabs = this.id;
      });
      if (this.contentEl?.nativeElement) {
        this.tabsService.heightTabActive = this.contentEl.nativeElement.offsetHeight;
      }
    }
    this.button = button;
    this.tabsService.buttons.sort((a: ButtonTabModel, b: ButtonTabModel) => {
      if (a.id < b.id) {
        return -1;
      }
      if (a.id > b.id) {
        return 1;
      }
      return 0;
    });
    if (this.tabsService.loadComplete) {
      this.tabsService.buttons = [];
      this.tabsService.tabsReloaded.emit(true);
    } else {
      if (this.gdaTabsService.animationsActivated) {
        this.animations = true;
      }
    }
  }

  /**
   * unsubscribe
   */
  ngOnDestroy(): void {
    this.animations = false;
    this.tabsService.buttons = this.tabsService.buttons.filter((button: ButtonTabModel) => button.id !== this.id);
    this.sub1.unsubscribe();
    this.sub2.unsubscribe();
    this.sub3.unsubscribe();
    const parent = this.renderer.parentNode(this.elementRef.nativeElement);
    this.elementRef.nativeElement.remove();
    parent.querySelectorAll('gda-tab').forEach((e: HTMLDivElement, i: number) => {
      e.setAttribute('data-gda-tab', String(i));
      this.tabsService.buttons[i].id = i
    });
    this.tabsService.tabsReloaded.emit();
  }
}
