export class GdaTabsStyleStatusModel {
  public backgroundColor: string; 
  public color: string;
  
  constructor(
    backgroundColor: string,
    color: string
  ) {
    this.backgroundColor = backgroundColor;
    this.color = color;
  }
} 

export class GdaTabsStyleModel {
  public normal: GdaTabsStyleStatusModel;
  public selected: GdaTabsStyleStatusModel;
  public barBackgroundColor: string
  constructor(
    normal: GdaTabsStyleStatusModel = { backgroundColor: '#ddd', color: '#000' },
    selected: GdaTabsStyleStatusModel = { backgroundColor: '#333', color: '#fff' },
    barBackgroundColor: string = '#000'
  ) {
    this.normal = normal;
    this.selected = selected;
    this.barBackgroundColor = barBackgroundColor;
  }
}